.travelers {
  background-color: #192144;
  border-bottom-left-radius: 31px;
  border-bottom-right-radius: 31px;
  position: relative;
}

.travelers::before {
  content: "";
  position: absolute;
  background-image: url("../img/smolov-mobile.png");
  background-repeat: no-repeat;
  width: 143px;
  height: 143px;
  top: 53px;
  right: 33px;
}

.travelers::after {
  content: "";
  position: absolute;
  background: url("../img/mobile-back.png");
  background-repeat: no-repeat;
  width: 113px;
  height: 132px;
  top: 37px;
  left: -5px;
}

.travelers__section-wrapper {
  z-index: 2;
  position: relative;
  width: 320px;
  margin: 0 auto;
}

.travelers__header {
  padding: 0;
  margin: 0;
  font-family: "BloggersansB", Arial, sans-serif;
  font-size: 24px;
  line-height: 24px;
  color: #ff8d30;
  padding: 66px 0 0 51px;
  margin: 0 0 0 0;
}

.travelers__description {
  padding: 0;
  margin: 20px 57px 21px 51px;
  font-family: "BloggersansL", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  position: relative;
}

.travelers__description::before {
  content: "";
  position: absolute;
  background-image: url("../img/rogovaya-mobile.png");
  background-repeat: no-repeat;
  width: 64px;
  height: 71px;
  top: 241px;
  left: -51px;
}

.travelers__promo {
  padding: 0;
  margin: 0px 0 56px 51px;
  font-family: "BloggersansB", Arial, sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}


@media (min-width: $tablet) {

  .travelers {
    padding-bottom: 221px;
  }

  .travelers::before {
    top: 472px;
    left: 55px;
  }

  .travelers::after {
    background: url("../img/back-tablet.png");
    width: 212px;
    height: 212px;
    top: 37px;
    left: -5px;
  }

  .travelers__section-wrapper {
    z-index: 2;
    position: relative;
    width: 768px;
    margin: 0 auto;
  }

  .travelers__header {
    font-size: 45px;
    line-height: 45px;
    margin: 0;
    padding: 137px 0 0 74px;
  }

  .travelers__description {
    font-size: 28px;
    line-height: 34px;
    margin: 57px 212px 38px 76px;
    position: relative;
  }

  .travelers__description::before {
    background-image: url("../img/rogovaya.png");
    width: 285px;
    height: 285px;
    top: -59px;
    left: 379px;
  }

  .travelers__promo {
    font-size: 21px;
    line-height: 24px;
    margin: 1px 379px 76px 75px;

  }

  .travelers__list {
    display: flex;
    justify-content: space-around;
  }

}

@media(min-width: $desktop) {

  .travelers::before {
    top: 134px;
    left: 620px;
  }

  .travelers::after {
    top: 70px;
    left: 132px;
}

  .travelers__section-wrapper {
    width: $content-width;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .travelers__header {
    padding: 127px 0 0 69px;
  }

  .travelers__description {
    line-height: 33px;
    margin: 56px 152px 38px 70px;
  }

  .travelers__description::before {
    top: 157px;
    left: 59px;
}

  .travelers__promo {
    margin: 0px 321px 0px 70px;
    padding-top: 3px;
}


  .button--travelers {
    position: absolute;
    bottom: -183px;
    width: 280px;
  }
}

